.main {
  padding: 10px;
  /* height: 100vh; */
  background: linear-gradient(
      to right,
      rgba(0, 98, 185, 0.8),
      rgba(0, 98, 185, 0.8)
    ),
    url(../images/common-bg.svg);
}
.layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar {
  /*  */
  /* position: fixed; */
  display: flex;
  flex-direction: column;
  background: transparent;
  /*  */
  padding: 0px 10px;
  min-height: 100%;
  width: 110px;
  border-radius: 5px;
  box-shadow: 0 2px 25px 12px rgba(0, 98, 185, 0.8);
  color: #fff;
  margin-top: 60px;
  margin-right: 10px;
  transition: width 1s;
}

.sidebar:hover {
  width: 250px;
}

.sidebar:hover .menu-item a {
  display: block;
}
.sidebar:hover .menu-item {
  justify-content: flex-start;
}
.sidebar:hover .active {
  background: rgb(238, 232, 233);
  width: 200px;
}

.menu {
  margin-top: 10px;
  /*  */
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  /*  */
}
.menu-item {
  /*  */
  padding: 10px 0px;
  list-style: none;
  display: flex;
  justify-content: center;
  /*  */
  margin-top: 30px;
}
.menu-item a {
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  /*  */
  white-space: nowrap;
  display: none;
}
.menu-item i {
  font-size: 1.2rem;
  margin: 0px 15px;
}

.content {
  /* border: 2px solid red; */
  width: 100%;
  height: 100%;
}
.header {
  height: 10vh;
  margin: 10px;
  /* border: 2px solid black; */
}
.body {
  height: 85vh;
  margin: 10px;
  /* border: 1px solid rgba(0, 98, 185, 0.8); */
  color: #fff;
}

.active {
  background: rgb(238, 232, 233);
  padding: 10px;
  width: 50px;
  transition: width 0.7s;
}
.active a,
.active i {
  color: black;
}
.menu .menu-item a:hover {
  color: black;
}
.header-content {
  margin: 0 15px;
  display: flex;
  align-items: center;
  justify-content: end;
  height: 45px;
}
.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  text-decoration: none;
  font-size: 1.2rem;
  color: #fff;
  margin: 0 10px;
  text-transform: uppercase;
}
.logout {
  padding-top: 100px;
}
.logout i {
  color: red;
}
.logout a {
  font-weight: 700;
  color: red;
}
/* logo */
.logo {
  position: absolute;
  top: 8px;
  left: 16px;
  font-size: 18px;
}
.logo img {
  width: 30%;
}
/* logo */
