.booking {
  box-sizing: border-box;
  padding: 50px 20px;
  margin: auto;
  width: 40%;
}
.bkcontent {
  text-align: center;
  padding: 20px 10px;
  background: rgba(0, 98, 185, 0.8);
  border-radius: 20px;
}
.bkcontent h5 {
  letter-spacing: 3px;
}
.bkcontent button {
  margin: 10px 40px;
}
.check {
  color: aliceblue;
  border: 2px solid rgb(26, 177, 26) !important;
  background: rgb(26, 177, 26) !important;
}
.check:hover {
  background: rgb(37, 209, 37) !important;
}
