.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  /* background: #009579; */
  background: linear-gradient(
      to right,
      rgba(0, 98, 185, 0.8),
      rgba(0, 98, 185, 0.8)
    ),
    url(../images/common-bg.svg);
  font-family: "Poppins", sans-serif;
}
.register-form {
  border: 2px solid #fff;
  border-radius: 8px;
  padding: 30px;
  width: 400px;
  /* height: 60vh; */
  background: #fff;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}
.Login-btn {
  color: #fff;
  background: rgba(0, 98, 185, 0.8);
  border-radius: 8px;
  font-size: 1.3rem;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 10px 128px;
  cursor: pointer;
  transition: 0.4s;
}
.Login-btn:hover {
  color: black;
  border: 2px solid rgba(0, 98, 185, 0.8);
  background: #f5f1f1;
}
.register-form p {
  font-size: 15px;
  font-weight: 450;
}
.m-2 {
  text-decoration: none;
  color: #009579;
  cursor: pointer;
}
