.card {
  margin-top: 20px;
  margin-left: 20px;
  border-radius: 10px;
  /* box-shadow: 0 0 10px 0 #bcbcbc; */
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 20px 20px;
}
.card-header {
  font-size: 1.2rem;
  margin: 0 auto;
  font-weight: 600;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 0px;
}

.card-body p {
  font-size: 0.875rem;
  line-height: 1;
  font-weight: 400;
}
.card:hover {
  transform: scale(1.04);
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0.048), 0 0px 0px rgba(0, 0, 0, 0.06);
}
